import { ref } from 'vue';
export default {
  setup() {
    const YxsOption = ref("");
    const adminDialogVisible = ref(false);

    const handleClose = done => {
      ElMessageBox.confirm('确定关闭对话框?', '温馨提示', {
        type: 'info',
        center: true
      }).then(() => {
        done();
      });
    };

    return {
      YxsOption,
      handleClose,
      adminDialogVisible
    };
  },

  data() {
    return {
      // 院系所信息
      YxsList: [],
      Yxs: {},
      // 二级管理员列表
      AdminList: [],
      // 添加二级管理员
      addAdminInfo: {
        yxsh: "",
        // 院系所号
        useraccount: "",
        // 账号
        userpasswd: "",
        // 密码
        userrealname: "" // 用户的真实姓名

      },
      search: ""
    };
  },

  created() {
    this.getYxsList();
  },

  methods: {
    clearAdminInfo() {
      this.addAdminInfo = {
        yxsh: "",
        useraccount: "",
        userpasswd: "",
        userrealname: ""
      };
    },

    // 重置表单
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.clearAdminInfo();
    },

    // 选择院系所
    changeYxs() {
      this.Yxs = JSON.parse(JSON.stringify(this.YxsOption));
      this.YxsOption = this.Yxs.dwmc;
      Loading.show();
      this.getYxsAdminListById(this.Yxs.dwh);
      Loading.hide();
    },

    // 获取所有院系所信息
    getYxsList() {
      this.$axios.get("/weixin-yxs/getAllYxs").then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.YxsList = data.data;
        }
      });
    },

    // 根据学院号 获取 二级管理员 列表
    getYxsAdminListById(id) {
      this.$axios.post("/weixin-yxsadmin/yxsAdminlist", {
        yxsh: id
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          this.AdminList = data.data;
        }
      });
    },

    // 添加管理员 弹窗
    addAdminDialog() {
      if (Object.keys(this.Yxs).length == 0) {
        ElMessage.error("请选择学院,如果旁边选择框没有选项,请刷新页面");
        return false;
      }

      this.clearAdminInfo();
      this.adminDialogVisible = true;
    },

    // 添加管理员
    addAdminButton(formName) {
      this.addAdminInfo.yxsh = this.Yxs.dwh;
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios.post("/weixin-yxsadmin/addAdmin", this.addAdminInfo).then(res => {
            const data = res.data;

            if (data.code == 200) {
              ElMessage.success("添加二级管理员成功");
              this.adminDialogVisible = false;
              this.getYxsAdminListById(this.Yxs.dwh);
            }
          });
        } else {
          ElMessage.error("表单不合法！！");
        }
      });
    },

    // 修改管理员状态
    changeAdminState(id, s) {
      this.$axios.post("/weixin-yxsadmin/changeState", {
        account: id,
        state: s
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          ElMessage.success("状态修改成功");
          Loading.show();
          this.getYxsAdminListById(this.Yxs.dwh);
          Loading.hide();
        }
      });
    },

    // 删除管理员
    deleteAdminButton(value) {
      this.$axios.post("/weixin-yxsadmin/delAdmin", {
        account: value.useraccount
      }).then(res => {
        const data = res.data;

        if (data.code == 200) {
          ElMessage.success("删除成功");
          Loading.show();
          this.getYxsAdminListById(this.Yxs.dwh);
          Loading.hide();
        }
      });
    },

    // 禁用管理员
    doAdminPowerButton(value) {
      this.changeAdminState(value.useraccount, "0");
    },

    // 解封管理员
    undoAdminPowerButton(value) {
      this.changeAdminState(value.useraccount, "1");
    }

  },
  computed: {
    getAdminFilter() {
      return this.AdminList.map(t => {
        t['state'] = t.userenable == "1" ? "正常" : "禁用";
        return t;
      }).filter(data => !this.search || data.userrealname.includes(this.search.toLowerCase()) || data.state.includes(this.search.toLowerCase()) || data.useraccount.includes(this.search.toLowerCase()));
    }

  }
};